@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Sarpanch:wght@400;500;600;700;800;900&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Sarpanch', sans-serif;
}

::-webkit-scrollbar {
  width: 7px;
}

::-webkit-scrollbar-track {
  background-color: rgba(1, 6, 37, 0.1);
}

::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: #9CF0FA;
}

.login-page {
  align-items: center;
  background-image: url(/cybertrivia/static/media/TriviaLogin.580b0b0c.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  display: flex;
  height: 100vh;
  justify-content: flex-start;
  position: relative;
  width: 100vw;
}

.form-card {
  align-items: center;
  background: rgba(1, 6, 37, 0.65);
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  height: 32vw;
  justify-content: space-around;
  margin-left: 6.5625vw;
  width: 25.0625vw;
}

.form {
  display: flex;
  flex-direction: column;
  grid-gap: 2.1875vw;
  gap: 2.1875vw;
  width: 16.97vw;
}

.form input {
  background-color: inherit;
  border: none;
  border-bottom: 1px solid #F6F5FC;
  color: #F6F5FC;
  font-size: 1vw;
  font-weight: 600;
  height: 3em;
  outline: none;
  transition: all 0.7s ease;
}

.form input:focus {
  border-bottom: 1px solid #FE75FE;
}

.form input:-webkit-autofill,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    -webkit-transition: background-color 5000s ease-in-out 0s;
    transition: background-color 5000s ease-in-out 0s;
    -webkit-text-fill-color: #F6F5FC !important;
}

.form input::-webkit-input-placeholder {
  color: #F6F5FC;
}

.form input:-ms-input-placeholder {
  color: #F6F5FC;
}

.form input::placeholder {
  color: #F6F5FC;
}

.login-button {
  -webkit-animation: flash 3s linear infinite;
          animation: flash 3s linear infinite;
  background-color: rgba(19, 19, 19, 0.4);
  cursor: pointer;
  color: #53E9F4;
  font-size: 32px;
  font-weight: 600;
  margin-top: 1.5625vw;
  margin-bottom: 0.9375vw;
  outline: none;
  padding: 5px 0;
}

.login-button:hover {
  -webkit-animation: none;
          animation: none;
  border: 2px solid #eda1f7;
  color: #eda1f7;
  text-shadow: 0 0 15px #e441fa;
}

.title {
  margin-top: 15px;
  width: 19.1875vw;
}

.logo {
  align-self: flex-start;
  margin-left: 26.3vw;
  margin-top: 6vh;
  width: 23vw;
}

@media screen and (max-width: 1100px) {
  .login-page {
    justify-content: center;
    flex-direction: column-reverse;
    grid-gap: 2em;
    gap: 2em;
  }

  .form-card {
    height: 21em;
    width: 16em;
    margin-left: 0;
    justify-content: center;
    grid-gap: 18px;
    gap: 18px;
    margin-top: 10px;
  }

  .logo {
    align-self: auto;
    margin-left: 0;
    width: 13em;
  }

  .form {
    grid-gap: 1em;
    gap: 1em;
    width: 12em;
  }

  .form input {
    font-size: 0.9em;
    transition: all 0.7s ease;
  }

  .title {
    margin-top: 0;
    width: 13.44em;
  }

  .login-button {
    font-size: 25px;
    margin-top: 20px;
    margin-bottom: 0;
  }
}

@media screen and (max-width: 600px) {
  .login-page {
    justify-content: center;
    grid-gap: 0.5em;
    gap: 0.5em;
  }

  .form-card {
    height: 22em;
    width: 17em;
    margin-bottom: 45px;
  }

  .form {
    margin-top: 8px;
  }
}

@-webkit-keyframes flash {
  0%, 18%, 20%, 50.1%, 60%, 65.1%, 80%, 90.1%, 92% {
    border: 2px solid rgba(19, 19, 19, 0);
    color: rgba(1, 6, 37, 0.4);
    text-shadow: none;
  }
  18.1%, 20.1%, 30%, 50%, 60.1%, 65%, 80.1%, 90%, 92.1%, 100% {
    border: 2px solid #9CF0FA;
    color: #a6f2fa;
    text-shadow: 0 0 15px #53E9F4;
  }
}

@keyframes flash {
  0%, 18%, 20%, 50.1%, 60%, 65.1%, 80%, 90.1%, 92% {
    border: 2px solid rgba(19, 19, 19, 0);
    color: rgba(1, 6, 37, 0.4);
    text-shadow: none;
  }
  18.1%, 20.1%, 30%, 50%, 60.1%, 65%, 80.1%, 90%, 92.1%, 100% {
    border: 2px solid #9CF0FA;
    color: #a6f2fa;
    text-shadow: 0 0 15px #53E9F4;
  }
}

.header {
  margin: 0 auto;
  height: 12vh;
  width: 50%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: rgba(1, 6, 37, 0.75);
  border-radius: 0 0 10px 10px;
  color: #F6F5FC;
  font-weight: 500;
}

.img-div {
  display: flex;
  grid-gap: 15px;
  gap: 15px;
  align-items: center;
}

.player-img {
  width: 60px;
  border-radius: 50%;
}

.player-name {
  text-transform: uppercase;
}

@media screen and (max-width: 1140px) {
  .header {
    width: 60%;
  }
}

@media screen and (max-width: 990px) {
  .game-section {
    width: 75%;
  }
}

@media screen and (max-width: 825px) {
  .header {
    width: 80%;
  }
}

@media screen and (max-width: 570px) {
  .header {
    width: 85%;
    font-size: 0.9em;
  }

  .img-div {
    grid-gap: 8px;
    gap: 8px;
  }

  .player-img {
    width: 40px;
  }
}

.game-page {
  background-image: url(/cybertrivia/static/media/TriviaBackground1-blur.dd3f8bbd.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  height: 100vh;
  width: 100vw;
}

.main-section {
  align-items: center;
  display: flex;
  height: 88vh;
  justify-content: center;
  width: 100%;
}

.game-section {
  background: rgba(1, 6, 37, 0.9);
  border: 1px solid #9CF0FA;
  box-shadow: 3px 3px 8px #9CF0FA;
  color: #F6F5FC;
  display: flex;
  flex-direction: column;
  height: 88%;
  padding: 15px 25px;
  width: 65%;
}

.timer {
  align-items: center;
  display: flex;
  font-size: 2.5em;
  grid-gap: 12px;
  gap: 12px;
}

.hourglass {
  -webkit-animation: rotate 2s infinite ease;
          animation: rotate 2s infinite ease;
  color: #9CF0FA;
  width: 30px;
}

.stop {
  -webkit-animation: none;
          animation: none;
}

.game-logo {
  align-self: center;
  max-width: 11em;
}

.question-div {
  align-items: center;
  display: flex;
  flex-direction: column;
  font-weight: 600;
  height: 41vh;
  justify-content: space-between;
  margin-top: 30px;
}

.question-text {
  font-size: 1.1em;
  max-width: 95%;
  word-wrap: break-word;
}

.answer-options {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  grid-gap: 12px;
  gap: 12px;
  justify-content: center;
  min-height: 2.6em;
  padding: 0 20px;
  grid-row-gap: 18px;
  row-gap: 18px;
  width: 60%;
}

.btn {
  border: none;
  border-radius: 3.5px;
  color: #010625;
  cursor: pointer;
  font-size: 0.9em;
  font-weight: 600;
  height: 3.375em;
  outline: none;
  width: 12.5em;
}

.correct-answer {
  background-color: rgb(68, 194, 72);
  color: #F6F5FC
}

.wrong-answer {
  background-color: rgb(236, 69, 69);
  color: #F6F5FC;
}

.correct-answer:hover, .wrong-answer:hover {
  color: #F6F5FC;
}

.next-btn {
  background-color: inherit;
  border: 2px solid #9CF0FA;
  color: #9CF0FA;
  cursor: pointer;
  font-size: 1.1em;
  font-weight: 600;
  outline: none;
  padding: 6px;
  width: 7em;
}

.next-btn:hover {
  border: 2px solid #eda1f7;
  color: #eda1f7;
}

.hidden {
  visibility: hidden;
}

@media screen and (max-width: 1140px) {
  .game-section {
    width: 75%;
  }

  .game-logo {
    max-width: 9em;
  }
}

@media screen and (max-width: 990px) {
  .game-section {
    width: 90%;
  }
}

@media screen and (max-width: 825px) {
  .answer-options {
    width: 85%;
  }
}

@media screen and (max-width: 600px) {
  .answer-options {
    width: 90%;
  }

  .question-text {
    font-size: 1em;
  }
}

@media screen and (max-width: 570px) {
  .game-section {
    padding: 15px 10px;
  }

  .game-logo {
    display: none;
  }

  .question-div {
    height: 65vh;
    margin-top: 10px;
    padding-bottom: 5px;
  }

  .question-category {
    font-size: 0.9em;
  }

  .next-btn {
    font-size: 1em;
  }

  .btn {
    font-size: 0.85em;
    height: 3.2em;
    width: 14.5em;
  }

  .question-text {
    font-size: 0.95em;
  }

  .timer {
    font-size: 2em;
    grid-gap: 8px;
    gap: 8px;
  }

  .hourglass {
    font-size: 23px;
  }
}

@media screen and (max-width: 400px) {
  .next-btn {
    font-size: 0.9em;
  }
}

@media screen and (max-height: 620px) {
  .timer {
    font-size: 1.5em;
    grid-gap: 6px;
    gap: 6px;
  }

  .hourglass {
    font-size: 18px;
  }

  .question-text, .question-category {
    font-size: 0.85em;
  }

  .next-btn, .btn {
    font-size: 0.8em;
  }
}

@media screen and (max-height: 580px) {
  .timer {
    font-size: 1.4em;
    grid-gap: 5px;
    gap: 5px;
  }

  .hourglass {
    font-size: 15px;
  }

  .question-text, .question-category {
    font-size: 0.80em;
  }

  .next-btn, .btn {
    font-size: 0.75em;
  }
}

@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
  to {
    -webkit-transform: rotateZ(180deg);
            transform: rotateZ(180deg);
  }
}

@keyframes rotate {
  from {
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
  }
  to {
    -webkit-transform: rotateZ(180deg);
            transform: rotateZ(180deg);
  }
}
